class UDSpinner extends React.Component {

  // state is for keeping control state before or after changes.
  state = {

    // the things you want to put in state.
    // text: this.props.text //un comment the line to use state insted props
  }
constructor(props) {
        super(props);
        this.state = {
          loading: true,
        };
    this.size = {
      size: this.props.size
    };
    this.state.color = {
      color: this.props.color
    };
    this.state.frontColor = {
      frontColor: this.props.color
    };
     this.state.backColor = {
      backColor: this.props.color
    };
    }

  render() {
    const { loading } = this.state;
    const loaders = {
BallSpinner,
BarsSpinner,
CircleSpinner,
CubeSpinner,
DominoSpinner,
FillSpinner,
FireworkSpinner,
FlagSpinner,
GridSpinner,
GuardSpinner,
HeartSpinner,
ImpulseSpinner,
PulseSpinner,
PushSpinner,
SequenceSpinner,
SphereSpinner,
SpiralSpinner,
StageSpinner,
SwapSpinner,
WaveSpinner,
ClapSpinner,
RotateSpinner,
SwishSpinner,
GooSpinner,
CombSpinner,
PongSpinner,
RainbowSpinner,
RingSpinner,
HoopSpinner,
FlapperSpinner,
MagicSpinner,
JellyfishSpinner,
TraceSpinner,
ClassicSpinner,
MetroSpinner,
WhisperSpinner
    }
      // These props are returned from PowerShell!
      // return <h1>{this.state.text}</h1> // un comment the line to render using value from state.

      return React.createElement(loaders[this.props.tagName], this.props)
  }
}

export default UDSpinner
